<template>
    <page class="collection">
        <div class="grid">
            <div class="cardGrid" @scroll="loadMoreDynamicly">
                <div class="mtgCard"
                    v-for="card in loadedCards"
                    :key="'mtg-' + card.id"
                    @mouseover="peekCardEnter(card)"
                    @mouseleave="peekCardLeave()"
                    @click="openCard(card)"
                    :class="{ selected: card.id === loadedCard.id }"
                >
                    <img :src="card.image_uris.normal" />
                    <h2>{{ card.name }}</h2>
                    <h3>x{{ card.MagicManager.count }}</h3>
                </div>
            </div>
            <div class="sideInfo">
                <card class="generalInfo">
                    <h1>Your collection</h1>
                    <p>You own a total of {{ totalCards }} cards</p>
                </card>
                <card class="detailedInfo" v-if="showCard">
                    <cardDetails v-if="showPeekCard" :card="peekCard"  :showCollectionControlls="true" />
                    <cardDetails v-if="!showPeekCard" :card="loadedCard"  :showCollectionControlls="true" />
                </card>
                <card class="cardList">
                    <textBox label="Search collection" v-model="query"></textBox>
                    <dropDown v-for="card in cards" :key="card.id" :label="cardName(card)">
                        <cardDetails :card="card"  :showCollectionControlls="true" />
                    </dropDown>
                </card>
                <card class="backup">
                    <button @click="backupFirestore()">Backup collection online</button>
                </card>
            </div>
        </div>
        <snackbar name="notLoggedIn">
            You are not logged in
        </snackbar>
        <snackbar name="backupDone">
            Backup was successful
        </snackbar>
        <snackbar name="backupFail">
            Backup was unsuccessful
        </snackbar>
    </page>
</template>

<script>
import { db } from "../js/global.js";
import { getList, backupOnline, trimmedCollection, loadFullCard } from "../js/collection.js";
import { page, card, textBox, dropDown, snackbar, performAction } from "../components.js";
import cardDetails from "../components/cardDetails.vue";
import vueChannel from "vue-channel";

export default {
    components: {
        page,
        card,
        textBox,
        dropDown,
        cardDetails,
        snackbar
    },
    data() {
        return {
            query: "",
            cards: [],
            loadedCards: [],
            loadedCard: {
                id: ""
            },
            showPeekCard: false,
            showCard: false,
            peekCard: {},
            queryCards: "",
            exportJson: ""
        }
    },
    computed: {
        totalCards() {
            if (this.cards.length > 0) {
                var amounts = this.cards.map(card => card.MagicManager.count);
                return amounts.reduce((last, next) => last + next);
            } else {
                return 0;
            }
        }
    },
    methods: {
        cardName(card) {
            if (card.MagicManager.count > 1) {
                return card.name + ` (x${card.MagicManager.count})`;
            } else {
                return card.name;
            }
        },
        peekCardEnter(card) {
            // this.showPeekCard = true;

            this.peekCard = Object.assign({}, this.peekCard, {
                ...card,
                trimmed: this.loadedCard.id === card.id ? false : true
            });
        },
        peekCardLeave() {
            // this.showPeekCard = false; 
        },
        openCard(card) {
            loadFullCard(card.id)
                .then(data => {
                    this.loadedCard = Object.assign({}, this.loadedCard, data);

                    this.showCard = true;
                });
        },
        backupFirestore() {
            backupOnline()
                .then(() => performAction("snackbar:backupDone"))
                .catch(err => {
                    console.log(err);
                    if (err === "auth/noUser") {
                        performAction("snackbar:notLoggedIn");
                    } else {
                        performAction("snackbar:backupFail");
                    }
                });
        },
        loadMoreDynamicly({ target: { scrollTop, clientHeight, scrollHeight }}) {
            console.log(scrollTop, clientHeight, scrollHeight)
            if (scrollTop + clientHeight >= (scrollHeight - 200)) {
                this.loadCards();
            }
        },
        loadCards(count = 25) {
            var additionalCards = this.cards.splice(0, count);

            if (additionalCards.length > 0) {
                this.loadedCards = [
                    ...this.loadedCards,
                    ...additionalCards
                ];
            }
        }
    },
    created() {
        this.cards = getList();
        this.loadCards();

        console.log(getList());

        vueChannel("collection")    
            .receive(state => {
                this.cards = getList();
            });
    }
}
</script>

<style lang="scss" scoped>

@import "../mdl-vue/config.scss";

@font-face {
    font-family: "Ringbearer";
    src: url(../fonts/Ringbearer.ttf);
}

.page {
    overflow: hidden;
}

$overviewThreshhold: 1200px;

@media screen and (min-width: $overviewThreshhold) {
    .grid {
        height: 100%;
        width: calc(100% - 32px);

        // padding: 16px;
        display: grid;
        grid-gap: 16px;
        grid-template-columns: repeat(12, 1fr);
        grid-template-rows: auto;

        .sideInfo {
            width: 100%;

            grid-column: 9 / 13;
            grid-row: 1 / 13;

            overflow-y: auto;

            .card {
                width: calc(100% - 32px);
                max-width: unset;
            }
        }

        .cardGrid {
            grid-column: 1 / 9;
            grid-row: 1 / 13;

            display: grid;

            grid-template-columns: repeat(5, 1fr);
            grid-template-rows: auto;
            overflow-y: auto;

            .mtgCard {
                cursor: pointer;

                img {
                    width: 80%;

                    margin: 8px auto;
                    display: block;

                    box-shadow: $elevation24dp;
                    border-radius: 12px;
                    transition: 250ms $globalDefaultCurve all;
                    transform: scale(1);
                }

                h2 {
                    margin-bottom: 0;

                    font-size: 16px;
                    font-family: "Ringbearer";
                    text-align: center;
                }
                
                h3 {
                    margin-top: 4px;
                    margin-bottom: 16px;

                    font-family: "Roboto";
                    font-size: 14px;
                    color: #555555;
                    text-align: center;
                }

                &:hover img {
                    transform: scale(1.03);
                }
            }
        }

        .card {
            max-width: 700px;
        }
    }

    .cardList {
        display: none;
    }
}

@media screen and (max-width: $overviewThreshhold) {
    .collection {
        .grid {
            display: block;
            height: 100%;
            
            .cardGrid {
                display: none;
            }
        }

        .detailedInfo {
            display: none;
        }

        .sideInfo {
            height: 100%;

            overflow-y: auto;

            .card {
                max-width: 700px;
            }
        }
    }
}

</style>